"use client"; // Ensure this is at the top of your file if using Next.js

import Input from "@/components/Input";
import React, { useState } from 'react';

export default function Contact() {
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setEmail(e.target.value);
  };

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setName(e.target.value);
  };

  const handleMessageChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setMessage(e.target.value);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      // Using FormSubmit.co with the correct endpoint format and activation
      const response = await fetch('https://formsubmit.co/ajax/dineshydv3505@gmail.com', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({
          email,
          name,
          message,
          _subject: "New Contact Form Submission",
          _template: "table"
        })
      });

      const data = await response.json();

      if (data.success) {
        setEmail('');
        setName('');
        setMessage('');
        setError('Message sent successfully!');
      } else {
        setError('Failed to send message. Please try again.');
      }
    } catch (error) {
      console.error('Error sending email:', error);
      setError(`An error occurred while sending your message. Please try again later. ${error}`);
    }
  };

  const isFormValid = email && name && message;

  return (
    <div className="">
      <h5 className="font-outfit lg:text-8xl font-bold">
        LET&#39;S WORK<br /> 
        <span className="text-[#7842BF] opacity-70">TOGETHER</span>
      </h5>

      <form onSubmit={handleSubmit} className="px-2 py-5 text-sm">
        <Input
          title="Email"
          titleClass="text-sm"
          type="email"
          placeholder="Your@email.com"
          value={email}
          onChange={handleEmailChange}
          className="mb-4"
        />
        <Input
          title="Name"
          titleClass="text-sm"
          type="text"
          placeholder="Your name"
          value={name}
          onChange={handleNameChange}
          className="mb-4"
        />
        <Input
          title="Message"
          titleClass="text-sm"
          type="textarea"
          rows={4}
          placeholder="Your message"
          value={message}
          onChange={handleMessageChange}
          className="mb-4"
        />
        {error && <p className={`text-sm ${error.includes('successfully') ? 'text-green-500' : 'text-red-500'}`}>{error}</p>}
        <button
          type="submit"
          disabled={!isFormValid}
          className="w-full py-2 px-4 mt-6 bg-[#7842BF] text-white rounded-lg disabled:opacity-50 disabled:cursor-not-allowed"
        >
          Send Message
        </button>
      </form>
    </div>
  );
}
